import * as actionType from './actionType'
import {getData} from 'util'
import {API_BILLSETUP_SEARCH} from 'api'
//同步
export const update = (object) => ({
  type: actionType.BILLSETUP_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.BILLSETUP_UPDATE, data: {tableLoading: true}})
  getData({api: API_BILLSETUP_SEARCH, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.BILLSETUP_UPDATE,
      data: {
        tableLoading: false,
        searchResult: data.result
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.BILLSETUP_FILE
    })
  })
}



