import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

// 将state数据挂载到props数据上面
@connect(
  state => ({...state.billSetUp, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '系统编号',
      width: 150,
      dataIndex: 'systemCode',
      key: 'systemCode'
    }, {
      title: '订单类型编号',
      width: 150,
      dataIndex: 'orderTypeCode',
      key: 'orderTypeCode'
    }, {
      title: 'WMS订单类型',
      width: 150,
      dataIndex: 'wmsOrderType',
      key: 'wmsOrderType'
    }, {
      title: '业务类型',
      width: 150,
      dataIndex: 'businessType',
      key: 'businessType'
    }, {
      title: '业务类型编号',
      width: 150,
      dataIndex: 'typeCode',
      key: 'typeCode'
    }, {
      title: '退货标识',
      width: 150,
      dataIndex: 'returnProductSign',
      key: 'returnProductSign'
    }, {
      title: '成本类型编号',
      width: 150,
      dataIndex: 'businessTypeCode',
      key: 'businessTypeCode'
    }]
  }

  componentDidMount() {
    const {handleGetList} = this.props
    handleGetList()
  }

  render() {
    console.log('测试')
    const {
      tableLoading,
      searchResult
    } = this.props
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          pagination={false}
        />
      </div>
    )
  }
}



